(function () {
    'use strict';
    angular.module('app.core').service('analyticsService', Analytics);

    Analytics.$inject = [];

    function Analytics() {

        function fireEvent(name) {
            window.dataLayer.push({
                'event': name
            });
        }

        function fireCustomEvent(category, action, label, value) {
            window.dataLayer.push({
                'event': 'customEvent',
                'eventCategory': category,
                'eventAction': action,
                'eventLabel': label,
                'eventValue': value
            });
        }

        function virtualPageView(pageTitle, pageUrl) {
            window.dataLayer.push({
                'event': 'analyticsVPV',
                'pageTitle': pageTitle,
                'pageUrl': pageUrl
            });
        }

        return {
            fireEvent: fireEvent,
            fireCustomEvent: fireCustomEvent,
            virtualPageView: virtualPageView
        };
    }
})();