(function () {
    'use strict';
    angular.module('app.password-reset')
        .controller('SubmitPasswordController', SubmitPasswordController);

    SubmitPasswordController.$inject = ['submitPasswordService', 'analyticsService'];

    function SubmitPasswordController(submitPasswordService, analyticsService) {
        var vm = this;

        //Properties
        vm.password = null;
        vm.serverErrorMessage = null;
        vm.passwordInvalid = false;
        vm.loading = false;
        vm.token = '';
        vm.tokenIsValid = false;

        //Functions
        vm.resetPassword = resetPassword;
        vm.hasValidationErrors = hasValidationErrors;

        activate();

        function activate() {
            checkToken();
        }

        //Public
        function resetPassword(form) {
            if (form.$valid) {
                vm.loading = true;
                submitPasswordService.resetPassword(vm.token, vm.password).then(passwordResetSuccess, passwordResetFailure);
            }
        }

        function hasValidationErrors(form) {
            return form.$invalid && form.$submitted && !vm.serverErrorMessage;
        }

        //Private
        function passwordResetSuccess() {
            vm.loading = false;
            vm.serverErrorMessage = null;
            vm.passwordInvalid = false;
            analyticsService.fireCustomEvent('forgotPassword', 'submitPassword', 'success');
            submitPasswordService.goToSuccessState();
        }

        function passwordResetFailure(response) {
            vm.loading = false;
            vm.serverErrorMessage = response.data.Message;
            vm.passwordInvalid = true;
            analyticsService.fireCustomEvent('forgotPassword', 'submitPassword', 'failure');
        }

        function checkToken() {
            vm.token = submitPasswordService.getResetToken();
            submitPasswordService.validateToken(vm.token).then(function () {
                vm.tokenIsValid = true;
            }, 
            redirectToInvalidToken);
        }

        function redirectToInvalidToken() {
            analyticsService.fireCustomEvent('forgotPassword', 'submitPassword', 'invalidToken');
            submitPasswordService.goToInvalidTokenEnterEmail();
        }
    }
})();