(function () {
    'use strict';
    angular.module('app.password-reset')
        .factory('confirmationService', ConfirmationService);

    ConfirmationService.$inject = ['$window', 'config'];

    function ConfirmationService($window, config) {
        return {
            returnToLogin: returnToLogin
        };

        function returnToLogin() {
            var url = config.legacyOnlineOrderUrl + '/Account/LogOn';
            $window.location.href = url;
        }
    }
})();