(function() {
    'use strict';
    angular.module('app.password-reset')
        .controller('ConfirmationController', ConfirmationController);

    ConfirmationController.$inject = ['confirmationService', 'analyticsService'];
    
    function ConfirmationController(confirmationService, analyticsService) {
        var vm = this;

        //Properties
        vm.email = null;        
        vm.title = "GOOD TO GO";
        vm.details = "Your password was successfully reset.";
        vm.button = "SIGN IN";

        //Functions
        vm.goToLogin = goToLogin;

        //Public
        function goToLogin() {
            analyticsService.fireCustomEvent('forgotPassword', 'confirmation', 'signInClick');
            confirmationService.returnToLogin();
        }
    }
})();