(function() {
    'use strict';
    angular.module('app.password-reset')
        .factory('submitPasswordService', SubmitPasswordService);

    SubmitPasswordService.$inject = ['api', '$state', '$location'];

    function SubmitPasswordService(api, $state, $location) {

        return {
            resetPassword: resetPassword,
            getResetToken: getResetToken,
            goToSuccessState: goToSuccessState,
            validateToken: validateToken,
            goToInvalidTokenEnterEmail: goToInvalidTokenEnterEmail
        };

        function resetPassword(token, password) {
            return api.resetPassword(token, password);
        }

        function getResetToken() {
            var params = $location.search();
            return params.id;
        }

        function validateToken(token) {
            return api.getPasswordResetInfo(token);
        }

        function goToSuccessState() {
            $state.go('password-reset.confirmation');
        }

        function goToInvalidTokenEnterEmail() {
            $state.go('password-reset.enter-email-token-invalid');
        }
    }
})();