(function () {
    'use strict';
    angular.module('app.entree')
        .controller('BurritoController', BurritoController);

    BurritoController.$inject = ['$stateParams','stateService'];

    function BurritoController($stateParams, stateService) {
    	var vm = this;
    	vm.fillingState = stateService.state.active;
        vm.rice = stateService.state.closed;
        vm.beans = stateService.state.closed;
        vm.toppings = stateService.state.closed;
        vm.sidesDrinks = stateService.state.closed;

        setState($stateParams.step);

        vm.selectFilling = function (item) {
        	setState('rice');
        };

        vm.selectRice = function () {
        	setState('beans');
        };

        vm.selectBeans = function () {
        	setState('toppings');
        };

        vm.selectToppings = function () {
        	setState('sides-drinks');
        };

        //test data
        vm.fillings = [{ title: 'steak' }, { title: 'chicken' }, { title: 'barbacoa' }, { title: 'fish' }];

        function setState(step) {
        	switch (step) {
        		case 'rice':
        			vm.fillingState = stateService.state.open;
        			vm.rice = stateService.state.active;
        			break;
        		case 'beans':
        			vm.fillingState = stateService.state.open;
        			vm.rice = stateService.state.open;
        			vm.beans = stateService.state.active;
        			break;
        		case 'toppings':
        			vm.fillingState = stateService.state.open;
        			vm.rice = stateService.state.open;
        			vm.beans = stateService.state.open;
        			vm.toppings = stateService.state.active;
        			break;
        		case 'sides-drinks':
        			vm.fillingState = stateService.state.open;
        			vm.rice = stateService.state.open;
        			vm.beans = stateService.state.open;
        			vm.toppings = stateService.state.open;
        			vm.sidesDrinks = stateService.state.active;
        			break;
        		default:
        			break;
        	}
        }
    }
})();