(function () {
    'use strict';
    angular
    .module('app.widgets')
    .factory('password', PasswordInputService);

    PasswordInputService.$inject = ['api', 'memory', '$q'];

    function PasswordInputService(api, memory, $q) {
        return {
            getPasswordSuccessAndScore: getPasswordSuccessAndScore
        };

        function getEntropy(phrase) {
            var uniqueSymbols = [],
                lower = false,
                upper = false,
                number = false,
                special = false,
                N = 0,  //N = count of potential symbols from pools used
                L,      //L = count of unique symbols used
                H;      //entropy bits	H = Log2( N^L )

            for (var c = 0; c < phrase.length; c++) {
                //track unique symbols
                if (uniqueSymbols.indexOf(phrase[c]) === -1) {
                    uniqueSymbols.push(phrase[c]);
                }

                //track pools used
                //Lowercase			a = 97						z = 122	
                if (phrase.charCodeAt(c) > 96 && phrase.charCodeAt(c) < 123) {
                    lower = true;
                    //Uppercase			A = 65						Z = 90	
                } else if (phrase.charCodeAt(c) > 64 && phrase.charCodeAt(c) < 91) {
                    upper = true;
                    //numbers			0 = 48						9 = 57	
                } else if (phrase.charCodeAt(c) > 47 && phrase.charCodeAt(c) < 58) {
                    number = true;
                    //special chars
                } else {	//this way unicode chars are still special
                    special = true;
                }
            }

            //N = count of potential symbols from pools used
            N += lower ? 26 : 0;
            N += upper ? 26 : 0;
            N += number ? 10 : 0;
            N += special ? 32 : 0;	//yes some special chars open larger pools but most of our users will just use ascii
            //we will not code for non-roman alphabet pools etc.

            //L = count of unique symbols used
            L = uniqueSymbols.length;

            //entropy bits	H = Log2( N^L )
            H = Math.round((Math.log(Math.pow(N, L))) / (Math.log(2)));

            return H;
        }

        //currently we have the following phases
        //  1 = not good enugh
        //  2 = passes our criteria but weak
        //  3 = passes our criteria and entropy >= 64
        function getScore(rules, password) {
            var score = 1,
                regex,
                match,
                testSuccess,
                scoreSuccess = true, //inocent until proven guilty
                success = true,
                entropy = getEntropy(password);

            if (rules.length < 1) {
                success = false;
            } else {
                for (var r = 0; r < rules.length; r++) {
                    regex = new RegExp(rules[r].Pattern, 'g');
                    match = regex.exec(password);
                    testSuccess = (match !== null);

                    //business rule: all Enforced rules must pass for overall success
                    if (success && rules[r].Enforced && !testSuccess) {
                        success = false;
                    }

                    if (scoreSuccess) {
                        //business rule: both entropy and regex must pass for a rule to be successful
                        if (testSuccess) {
                            testSuccess = (entropy >= parseInt(rules[r].Entropy));
                        }

                        //business rule: A new grade is reached when ALL tests for that grade have passed
                        if (testSuccess) {
                            score = rules[r].Grade;
                        } else {
                            score = rules[r].Grade - 1;
                            scoreSuccess = false;
                        }
                    }
                }
            }

            return { pass: success, score: score, maxScore: rules.length };
        }

        function getPasswordSuccessAndScore(password) {
            var rules,
                passwordRulesKey = 'PasswordRulesKey',
                deferred = $q.defer();

            rules = memory.getCachedItem(passwordRulesKey);
            if (rules === null) {
                api.getPasswordCriteria()
                .then(function (data) {
                    rules = data.data;
                    memory.setCachedItem(passwordRulesKey, rules);

                    deferred.resolve(getScore(rules, password));
                });
            } else {
                deferred.resolve(getScore(rules, password));
            }

            return deferred.promise;
        }
    }
})();