(function () {
    'use strict';
    angular.module('app.entree')
        .controller('BeansController', BeansController);

    BeansController.$inject = [];

    function BeansController() {
        var vm = this;

        vm.showBeans = function () {
        	return vm.state === 'active' || vm.state === 'open';
        };

        vm.chooseBeans = function () {
            vm.onSelect();
        };
    }
})();