(function() {
    'use strict';
    angular.module('app.services').factory('api', Api);

    Api.$inject = ['$http', 'config'];

	/* istanbul ignore next */	//unit testing this file would be silly
    function Api($http, config) {

        return {
            resetPassword: resetPassword,
            initiatePasswordReset: initiatePasswordReset,
            getPasswordCriteria: getPasswordCriteria,
            getPasswordResetInfo: getPasswordResetInfo,
            getInviteeInfo: getInviteeInfo
        };

        function resetPassword(token, password) {
            var data = {
                resetToken: token,
                password: password
            };
            return post('/customer/resetpassword', data);
        }

        function initiatePasswordReset(email) {
            var data = {
                email: email
            };
            return post('/customer/initiatepasswordreset', data);
        }

        function getPasswordCriteria() {
            return get('/customer/passwordcriteria');
        }

        function getPasswordResetInfo(token) {
            return get('/customer/passwordresetinfo?resetToken=' + token);
        }

        function getInviteeInfo(inviteeid) {
        	return get('/customer/GetInviteeInfo?inviteeid=' + inviteeid);
        }

        function getMenu() {
        	return get('/menu/get');
        }

        function endpointUrl(path) {
            return config.apiBaseUrl + path;
        }

        function get(path) {
            return $http.get(endpointUrl(path));
        }

        function post(path, data) {
            return $http.post(endpointUrl(path), data);
        }
    }
})();