(function () {
	'use strict';
	angular.module('app.entree')
    .controller('EntreeController', EntreeController);

	EntreeController.$inject = ['api', '$stateParams'];

	function EntreeController(api, $stateParams) {
	    var vm = this;
	    vm.selectedItems = [];
	    vm.selectItem = function (item) {
	        vm.selectedItems.push(item);
	    };
	    vm.removeItem = function (item) {
	        function checkTitle(item) {
	            return function (val) {
	                return item.title !== val.title;
	            };
	        }
	        vm.selectedItems = vm.selectedItems.filter(checkTitle(item));
	    };
	}
})();