(function() {
    'use strict';
    angular.module('app.password-reset')
        .factory('enterEmailService', EnterEmailService);

    EnterEmailService.$inject = ['api', '$state', '$window', 'config'];

    function EnterEmailService(api, $state, $window, config) {
        return {
            initiateReset: initiateReset,
            cancelReset: cancelReset,
            goToEmailSent: goToEmailSent
        };

        function initiateReset(email) {
            return api.initiatePasswordReset(email);
        }

        function goToEmailSent(email) {
            var params = { email: email };
            $state.go('password-reset.email-sent', params);
        }

        function cancelReset() {
            $window.location.href = config.legacyOnlineOrderUrl;
        }
    }
})();