(function () {
    'use strict';
	angular.module('app.widgets')
    .directive('hero', hero);

	/**
	* @desc hero directive will set a hero image and text that can be upated in real time
	* @example <hero img-class="welcome" big-text="HELLO" small-text="world"></hero>
	*/
	function hero() {
		var directive = {
			restrict: 'E',
			templateUrl: 'dist/templates/hero/hero.html',
			controller: HeroController,
			controllerAs: 'vm'
		};
		return directive;
	}

	HeroController.$inject = ['$attrs'];
	function HeroController($attrs) {
		var vm = this;

        vm.imgClass = $attrs.imgClass;
        vm.bigText = $attrs.bigText;
        vm.smallText = $attrs.smallText;
	}
})();