(function () {
    'use strict';
    angular.module('app.entree')
        .controller('ToppingsController', ToppingsController);

    ToppingsController.$inject = [];

    function ToppingsController() {
        var vm = this;

        vm.showToppings = function () {
        	return vm.state === 'active' || vm.state === 'open';
        };

        vm.chooseToppings = function () {
            vm.onSelect();
        };
    }
})();