(function () {
	'use strict';
	angular
    .module('app.widgets')
    .directive('stickAndSlide', stickAndSlide);

	stickAndSlide.$inject = ['$window', '$document'];
	function stickAndSlide($window, $document) {
		var directive = {
			restrict: 'EA',
			link: link,
			bindings: { onUpdate: '&' }
		};
		return directive;

		function link(scope, element, attrs) {
			angular.element($window).bind("scroll", function () {
				var pin = parseInt(attrs.stickAt);
				var scroll = this.scrollY;

				console.log(element[0].getBoundingClientRect());
				//console.log([scroll, this.outerHeight - scroll]);
				if (attrs.fromBottom !== undefined && attrs.fromBottom.toLowerCase() === 'true') {
					pin = this.outerHeight - pin;

					if (scroll <= pin) {
						element.addClass(attrs.stickClass);
					} else {
						element.removeClass(attrs.stickClass);
					}
				} else {
					if (scroll >= pin) {
						element.addClass(attrs.stickClass);
					} else {
						element.removeClass(attrs.stickClass);
					}
				}
			});
		}
	}
})();