(function () {
    'use strict';
    angular.module('app.password-reset')
        .controller('EnterEmailController', EnterEmailController);

    EnterEmailController.$inject = ['enterEmailService', '$stateParams', 'analyticsService'];

    function EnterEmailController(enterEmailService, $stateParams, analyticsService) {
        var vm = this;
        //Properties
        vm.email = null;
        vm.serverErrorMessage = null;
        vm.isInvalidTokenRedirect = false;
        vm.loading = false;
        vm.message = 'Happens to the best of us. Enter your email address to get your reset on.';
        vm.emailRegex = /[a-z0-9!#$%&'*+\/=?^_`{|}~.-]+@[a-z0-9]([a-z0-9-]*[a-z0-9])?(\.[a-z0-9]([a-z0-9-]*[a-z0-9])?)+/i;

        //Functions
        vm.initiateReset = initiateReset;
        vm.cancelReset = cancelReset;
        vm.gaIsEmailValid = gaIsEmailValid;
        vm.hasValidationErrors = hasValidationErrors;

        activate();

        function activate() {
            if ($stateParams.tokenInvalid) {
                vm.message = "This link has expired, but we'll send you a new one if you enter your email address below.";
            }
        }

        //Public
        function initiateReset(form) {
            if (form.$valid) {
                vm.loading = true;
                enterEmailService.initiateReset(vm.email).then(initiateResetSuccess, initiateResetFailure);
            }
        }        

        function cancelReset() {
            analyticsService.fireEvent('forgotPasswordEnterEmailCancel');
            enterEmailService.cancelReset();
        }

        function gaIsEmailValid(valid) {
            analyticsService.fireCustomEvent('forgotPassword', 'submitEmailIsValid', valid.toString());
        }

        function hasValidationErrors(form) {
            return form.$submitted && form.$invalid && !vm.serverErrorMessage;
        }
        
        //Private
        function initiateResetSuccess() {
            vm.loading = false;
            vm.serverErrorMessage = null;
            analyticsService.fireEvent('forgotPasswordEnterEmailSuccess');
            enterEmailService.goToEmailSent(vm.email);
        }

        function initiateResetFailure(response) {
            vm.loading = false;
            vm.serverErrorMessage = response.data.Message;
            analyticsService.fireEvent('forgotPasswordEnterEmailFailure');
        }
    }
})();