(function () {
    'use strict';
    angular.module('app.entree')
        .controller('SidesDrinksController', SidesDrinksController);

    SidesDrinksController.$inject = [];

    function SidesDrinksController() {
    	var vm = this;

    	vm.showSidesDrinks = function () {
    		return vm.state === 'active' || vm.state === 'open';
    	};

    	vm.chooseSidesDrinks = function () {
    		vm.onSelect();
    	};
    }
})();