(function () {
	'use strict';
	angular.module('app.group-order')
    .controller('GroupOrderController', GroupOrderController);

	GroupOrderController.$inject = ['api', '$stateParams', '$state'];

	function GroupOrderController(api, $stateParams, $state) {
		var vm = this;
		vm.inviteeEmail = '';
		vm.inviterEmail = '';
		vm.inviterName = '';
		vm.loading = true;

		if (vm.inviterName === '') {
			api.getInviteeInfo($stateParams.inviteeid)
			.then(function (data) {
				vm.inviteeEmail = data.data.InviteeEmail;
				vm.inviterEmail = data.data.InviterEmail;
				vm.inviterName = data.data.InviterName;
				vm.loading = false;
			});
		}
	}
})();