(function () {
    'use strict';
    angular.module('app.password-reset')
        .controller('EmailSentController', EmailSentController);

    EmailSentController.$inject = ['$stateParams'];

    function EmailSentController($stateParams) {
        var vm = this;
        vm.email = null;
        vm.header = 'EMAIL SENT';
        vm.resetText = 'We just emailed a reset link to';
        vm.checkSpamText = "If you don't see it within a few minutes, check your spam and trash folders.";

        activate();

        function activate() {
            vm.email = $stateParams.email;
        }
    }
})();