(function () {
    'use strict';
    angular.module('app.services')
    .factory('memory', Memory);

    Memory.$inject = [];

    function Memory() {

        return {
            setItem: setItem,
            getItem: getItem,
            setCachedItem: setCachedItem,
            getCachedItem: getCachedItem,
            clearAll: clearAll
        };

        function setItem(key, value) {
            window.localStorage.setItem(key, JSON.stringify(value));
        }

        function getItem(key) {
            return JSON.parse(window.localStorage.getItem(key));
        }

        function setCachedItem(key, value, days) {
            value = JSON.stringify(value).replace(/;/g, '~~');

            if (days !== undefined) {
                var expires = new Date();
                expires.setDate(expires.getDate() + days);
                value += '; expires=' + expires.toUTCString();
            }

            window.document.cookie = key + '=' + value + '; path=/';
        }

        function getCachedItem(key) {
            var value = null,
                regex = new RegExp(key + '=([^;]+)'),
                match = regex.exec(window.document.cookie);

            if (match !== null) {
                value = JSON.parse(match[1].replace(/~~/g, ';'));
            }

            return value;
        }

        function clearAll() {
            var cookies = document.cookie.split(';');

            for (var i = 0; i < cookies.length; i++) {
                var index = cookies[i].indexOf('=');
                var name = index !== -1 ? cookies[i].substr(0, index) : cookies[i];
                window.document.cookie = name + '=;expires=Thu, 01 Jan 1970 00:00:00 GMT';
            }

            window.localStorage.clear();
        }
    }
})();