(function () {
    'use strict';
    angular.module('app.entree')
        .controller('RiceController', RiceController);

    RiceController.$inject = [];

    function RiceController() {
    	var vm = this;

    	vm.showRice = function () {
    		return vm.state === 'active' || vm.state === 'open';
        };

        vm.chooseRice = function () {
            vm.onSelect();
        };
    }
})();