(function () {
    angular.module('app').config(RouteTable)
        .run(PageChange);

    PageChange.$inject = ['$rootScope', '$window', '$location', 'analyticsService'];
    RouteTable.$inject = ['$stateProvider', '$urlRouterProvider', 'config'];

    function PageChange($rootScope, $window, $location, analyticsService) {
        $rootScope.$on('$stateChangeSuccess', function (event, toState, toParams, fromState, fromParams) {
            var baseTitle = 'Chipotle';
            $window.document.title = baseTitle;
            if (toState.data && toState.data.pageTitle) {
                var pageTitle = baseTitle + " - " + toState.data.pageTitle;
                $window.document.title = pageTitle;
                var pageUrl = $location.path();
                analyticsService.virtualPageView(pageTitle, pageUrl);
            }
        });
    }

    function RouteTable($stateProvider, $urlRouterProvider, config) {

        //$urlRouterProvider.otherwise(function () {
        //    window.location.href = config.legacyOnlineOrderUrl;
        //});
        $urlRouterProvider.otherwise('/group-order?storeid=60999&orderid=51099419&inviteeid=972436e2-edee-4b5c-8669-ff61ac4cfd84');

        $stateProvider
            .state('password-reset', {
                url: '/password-reset',
                abstract: true,
                template: '<password-reset/>'
            })
            .state('password-reset.enter-email', {
                url: '/enter-email',
                template: '<password-reset-enter-email/>',
                data: { pageTitle: 'Password Reset Enter Email' }
            })
            .state('password-reset.enter-email-token-invalid', {
                url: '/enter-email-token-invalid',
                params: { tokenInvalid: true },
                template: '<password-reset-enter-email/>',
                data: { pageTitle: 'Password Reset Invalid Token' }
            })
            .state('password-reset.email-sent', {
                url: '/email-sent',
                template: '<password-reset-email-sent/>',
                params: { email: null },
                data: { pageTitle: 'Password Reset Email Sent' }
            })
            .state('password-reset.confirmation', {
                url: '/confirmation',
                template: '<password-reset-confirmation/>',
                data: { pageTitle: 'Password Reset Confirmation' }
            })
            .state('password-reset.submit-password', {
                url: '/submit-password',
                template: '<password-reset-submit-password/>',
                data: { pageTitle: 'Password Reset Submit Password' }
            })
			.state('group-order', {
			    url: '/group-order?storeid&orderid&inviteeid',
			    template: '<group-order/>',
			    params: { storeId: '0', orderId: '0', inviteeId: '00000000-0000-0000-0000-000000000000' }
			})
			.state('entree', {
			    url: '/entree',
			    template: '<entree/>',
			})
        		.state('entree.burrito', {
        			url: '/burrito?step',
        			template: '<cmg-burrito/>',
        		})
        	.state('sides-drinks', {
        	    url: '/sides-drinks',
        	    template: '<sides-drinks/>',
        	});
    }
})();