(function () {
    'use strict';
    angular.module('app.widgets')
        .controller('PasswordInputController', PasswordInputController);
    PasswordInputController.$inject = ['$scope', '$timeout', 'password'];
    function PasswordInputController($scope, $timeout, password) {
        var vm = this;
        //Properties
        vm.type = "password";
        vm.togglePasword = false;
        vm.state = "wrong";
        vm.passwordRegex = /(?:((?=.*[a-z])(?=.*[A-Z]))|((?=.*[a-z])(?=.*[0-9]))|((?=.*[A-Z])(?=.*[0-9]))|((?=.*[a-z])(?=.*[^a-zA-Z0-9]))|((?=.*[A-Z])(?=.*[^a-zA-Z0-9]))|((?=.*[0-9])(?=.*[^a-zA-Z0-9])))(?:^[^\n\t\r\v\f]{8,50}$)(?![\r\n])/;

        //Functions
        vm.toggleCheckbox = toggleCheckbox;
        vm.change = change;

        //watch is for future improvements - split into 2 directives : just indicator + password
        //validation and checkbox directive; right now this is one directive(component)
        $scope.$watch(function () { return vm.state; }, function (newValue, oldValue) {
            if (newValue === "ok") vm.color = { success: true };
            if (newValue === "weak") vm.color = { ok: true };
            if (newValue === "wrong") vm.color = { fail: true };
        });

        //Public
        function change() {
            if (vm.password === undefined || vm.password === null) {
                vm.state = 'wrong';
                return;
            }

            password.getPasswordSuccessAndScore(vm.password)
                .then(function(result) {
                        if ((result.pass === true) && (result.score === result.maxScore)) {
                            vm.state = "ok";
                        } else if (result.pass === true) {
                            vm.state = "weak";
                        }
                        if (result.pass === false) {
                            vm.state = "wrong";
                        }
                    });
        }

        function toggleCheckbox() {
            if (vm.togglePassword === true) {
                vm.type = "text";
            } else {
                vm.type = "password";
            }
        }
    }
})();